<template>
  <div id="MtConsumer_component" class="container">
      <a-form class="filter_box" :form="formData" layout="inline">
        <a-form-item> <a-input style="width:100%;" v-model="iptContent" placeholder="" /></a-form-item>
        <a-form-item>
     <a-button icon="search" @click="initData()"  type="primary"></a-button>
   <a-button icon="plus" style="margin-left:20px;"  type="primary" @click="showModal"></a-button>
        </a-form-item>
      </a-form>
      <a-modal title="" v-model="visible" @cancel="cancelModal" @ok="saveModal" okText="" cancelText="">
        <a-form :form="form">
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['userName', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['userId', { rules: [{ required: true, message: '' }, { validator: handleUserId}] }]" />
          </a-form-item>

          <a-form-item label="" class="stepFormText" :label-col="labelCol" :wrapper-col="wrapperCol" hasFeedback>
            <a-input type="password" v-decorator="['passWord', { rules: [{ required: true, message: '' }, { validator: handlePass }] }]" name="password" />
          </a-form-item>
          <a-form-item label="" class="stepFormText" :label-col="labelCol" :wrapper-col="wrapperCol" hasFeedback>
            <a-input
              type="password"
              v-decorator="['confirm_password', { rules: [{ required: true, message: '' }, { validator: handleConfirmPass }] }]"
              name="confirm_password"
            />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-select v-decorator="['roleTypeId', { rules: [{ required: true, message: '' }] }]">
              <a-select-option :value="select.roleTypeId" :key="index" v-for="(select,index) in selects">{{select.roleTypeName}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['phone', { rules: [{ required: true, message: '' }, { validator: handIphone }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['zhixin', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['wechat', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['dingDing', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['mail', { rules: [{
              type: 'email', message: '',
            },{ required: true, message: '' }] }]" />
          </a-form-item>
        </a-form>
           <a
                    style="margin-left: 50%;"
                    @click="toggle"
                  >
                    <a-icon :style="{fontSize: '30px' }" :type="expand ? 'caret-up' : 'caret-down'" />
                  </a>
      </a-modal>
      <a-table id="table_blue" :pagination="pageOptions" :columns="columns" :dataSource="data">
        <template slot="action" slot-scope="text, record">
          <a-row>
            <a-col :span="8"><a v-if="record.deletFlg" href="javascript:;" @click="deleData(record)"></a></a-col>
            <a-col :span="8"><a href="javascript:;" @click="showModalEdit(record)"></a></a-col>
            <a-col :span="8"><a href="javascript:;" @click="detailsData(record)"></a></a-col>
          </a-row>
        </template>
      </a-table>
      <a-modal title="" v-model="visibleEdit" @cancel="cancelModalEdit" @ok="saveModalEdit" okText="" cancelText="">
        <a-form :form="formEdit">
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['userName', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item label="" class="stepFormText" :label-col="labelCol" :wrapper-col="wrapperCol" hasFeedback>
            <a-input type="password" v-decorator="['oldPassWord', { rules: [{ required: true, message: '' }] }]" name="password" />
          </a-form-item>
          <a-form-item label="" class="stepFormText" :label-col="labelCol" :wrapper-col="wrapperCol" hasFeedback>
            <a-input
              type="password"
              v-decorator="['passWord', { rules: [{ required: true, message: '' }, { validator: handlePassEdit }] }]"
              name="confirm_password"
            />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-select v-decorator="['roleTypeId', { rules: [{ required: true, message: '' }] }]">
              <a-select-option :value="select.roleTypeId" :key="index" v-for="(select,index) in selects">{{select.roleTypeName}}</a-select-option>
            </a-select>
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['phone', { rules: [{ required: true, message: '' }, { validator: handIphone }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback>
            <a-input id="warning" v-decorator="['zhixin', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['wechat', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['dingDing', { rules: [{ required: true, message: '' }] }]" />
          </a-form-item>
          <a-form-item :label-col="labelCol" :wrapper-col="wrapperCol" label=":" hasFeedback :style="{ display: expand ? 'block' : 'none' }">
            <a-input id="warning" v-decorator="['mail', { rules: [{
              type: 'email', message: '',
            },{ required: true, message: '' }] }]" />
          </a-form-item>
        </a-form>
        <a
                 style="margin-left: 50%;"
                 @click="toggle"
               >
                 <a-icon :style="{fontSize: '30px' }" :type="expand ? 'caret-up' : 'caret-down'" />
               </a>
      </a-modal>
  </div>
</template>
<script>
import request from '../utils/request'
const columns = [
  {
    title: '',
    align: 'center',
    dataIndex: 'userName',
    width: '12%'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'userId',
    width: '15%'
  },

  {
    title: '',
    align: 'center',
    dataIndex: 'roleTypeName',
    width: '15%'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'phone',
    width: '15%'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'createTime',
    width: '15%'
  },
  {
    title: '',
    align: 'center',
    dataIndex: 'createUser',
    width: '12%'
  },
  {
    title: '',
    align: 'center',
    dataIndex: '',
    scopedSlots: { customRender: 'action' },
    width: '16%'
  }
]

export default {
  name: 'mt-consumer-page',
  data () {
    return {
      formData: this.$form.createForm(this),
      form: this.$form.createForm(this), // form
      formEdit: this.$form.createForm(this), // form
      password: '', // 
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 15 }
      },
      expand: false, // 
      iptContent: null, // 
      visible: false, // model
      visibleEdit: false, // model
      columns,
      data: [], // table
      selects: [], // 
      UserId: 'root', // 
      pageSize: 10, // 
      page: 1, // 
      details: {}, // 
      formNewData: {}, // 
      formNewDataEdit: {}, // 
      receiveEdit: {},
      pageOptions: {// 
        defaultPageSize: 10,
        showQuickJumper: true,
        showSizeChanger: true,
        pageSizeOptions: ['10', '20', '50'],
        total: 0,
        onShowSizeChange: (current, size) => { // 
          this.pageSize = size
          this.page = current
          this.initData()
        },
        onChange: (page, pageSize) => {
          // 
          this.page = page
          this.initData()
        }
      }
    }
  },
  created () {
    this.initData()
  },
  methods: {
    toggle  () { // 
      this.expand = !this.expand
    },
    initData () {
      // table
      request({
        url: 'Service/API/V1/CHP/user/list',
        method: 'post',
        data: {
          keyWord: this.iptContent,
          userId: this.UserId,
          currentPage: this.page,
          pageNum: this.pageSize
        }
      })
        .then(res => {
          this.pageOptions.total = res.data.totalCount
          this.data = res.data.userJsons
        })
        .catch(() => {
          // alert('error')
        })
    },
    selectData () { // 
      request({
        url: 'Service/API/V1/CHP/role/list/' + this.UserId,
        method: 'get'
      })
        .then(res => {
          this.selects = res.data
        })
        .catch(() => {
          // alert('error')
        })
    },

    saveModal (e) { // 
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.formNewData = {createUser: this.UserId}
          for (let value in values) {
            if (value !== 'confirm_password') {
              this.formNewData[value] = values[value]
            }
          }
          request({
            url: 'Service/API/V1/CHP/user/create',
            method: 'post',
            data: this.formNewData
          })
            .then(res => {
              this.visible = false
              this.initData()
            })
            .catch(() => {
              // alert('error')
            })
        }
      })
    },
    saveModalEdit (e) { // 
      e.preventDefault()
      this.formEdit.validateFields((err, values) => {
        if (!err) {
          this.formNewDataEdit = {userId: this.receiveEdit.userId, customerName: values.userName, customerId: this.receiveEdit.customerId}
          for (let value in values) {
            this.formNewDataEdit[value] = values[value]
          }

          request({
            url: 'Service/API/V1/CHP/user/updateUser',
            method: 'put',
            data: this.formNewDataEdit
          })
            .then(res => {
              this.visibleEdit = false
              this.initData()
            })
            .catch((error) => {
              this.$error({
                title: '',
                content: error.msg
              })
            })
        }
      })
    },
    showModal () { // 
      this.visible = true
      this.expand = false
      this.form.resetFields() // 
      this.selectData()
    },
    showModalEdit (data) { // 
      this.visibleEdit = true
      this.expand = false
      this.formEdit.resetFields() // 
      // this.editUserId=data.userId
      request({
        url: 'Service/API/V1/CHP/user/detail/' + data.userId,
        method: 'get'
      })
        .then(res => {
          this.receiveEdit = res.data
          setTimeout(() => {
            this.formEdit.setFieldsValue({// 
              'zhixin': res.data.zhixin,
              'userName': res.data.customerName,
              'roleTypeId': res.data.roleTypeId,
              'phone': res.data.phone,
              'dingDing': res.data.dingDing,
              'mail': res.data.mail,
              'wechat': res.data.wechat
            })
          }, 100)
        })
        .catch(() => {
          // alert('error')
        })

      this.selectData()// 
    },
    cancelModal () { // model
      this.visible = false
    },

    cancelModalEdit () { // model
      this.visibleEdit = false
    },

    deleData (data) { // model
      let thisName = this // this
      this.$confirm({
        title: '',
        content: ' "' + data.userName + '" ?',
        okText: '',
        centered: true,
        width: 500,
        // iconType: 'exclamation-circle',
        cancelText: '',
        onOk () {
          request({
            url: 'Service/API/V1/CHP/user/delete/' + data.userId,
            method: 'delete'
          })
            .then(res => {
              thisName.initData()
            })
            .catch(() => {
              // alert('error')
            })
        },
        onCancel () {
        }
      })
    },
    handlePassEdit (rule, value, callback) {
      // 
      var mPasswordEdit = /^[!-~]{8,14}$/
      if (!value.match(mPasswordEdit)) {
        callback(new Error('8-14'))
        return
      }
      callback()
    },
    handleUserId (rule, value, callback) {
      // 
      var mUserId = /^[\u4e00-\u9fa5]{1,7}$|^[\dA-Za-z_]{1,14}$/
      if (!value.match(mUserId)) {
        callback(new Error('147'))
        return
      }
      callback()
    },
    handlePass (rule, value, callback) {
      // 
      this.password = value
      var mPassword = /^[!-~]{8,14}$/
      if (!value.match(mPassword)) {
        callback(new Error('8-14'))
        return
      }
      callback()
    },
    handleConfirmPass (rule, value, callback) {
      // 
      if (this.password && this.password !== value) {
        callback(new Error('!'))
        return
      }
      // Note:  callback validateFieldsAndScroll 
      callback()
    },
    handIphone (rule, value, callback) {
      // 
      var mPattern = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/
      if (!value.match(mPattern)) {
        callback(new Error('!'))
        return
      }
      callback()
    },
    detailsData (data) { // model
      this.details = {}
      // 
      request({
        url: 'Service/API/V1/CHP/user/detail',
        method: 'post',
        data: {
          custormId: String(data.customerId),
          userId: data.userId
        }
      })
        .then(res => {
          this.details = res.data
          this.$info({
            title: '',
            // JSX support
            content: (
              <div class="detailsConter">
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.customerName}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.roleTypeName}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.phone}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.zhixin}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.wechat}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.dingDing}
                </p>
                <p>
                  <span style="width:25%;display:inline-block;text-align: right;margin-right: 20px;">:</span>
                  {this.details.mail}
                </p>
              </div>
            ),
            okText: '',
            centered: true,
            width: 500,
            onOk () {
            }
          })
        })
        .catch(() => {
          // alert('error')
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.filter_box{
  text-align: right;
  margin-bottom: 20px;
}
.container {
  padding:20px;
}
</style>



