<template>
  <div id="MtClass_component" class="common-container">
    <div class="param_btn" v-if="scheduleList.length>0">
      <a-button type="primary" @click="editStatus = true" v-if="!editStatus"></a-button>
      <div v-else>
        <a-button @click="cancelSave"></a-button>
        <a-button style="margin-left:20px;" type="primary" @click="save()"></a-button>
      </div>
    </div>
    <div class="class_content">
      <h2 class="title"></h2>
      <div class="class_container">
        <a-form :form="form" ref="form" >
          <div class="item_class">
            <a-row :gutter="20" class="row_title">
              <a-col :span="5">
                <p></p>
              </a-col>
              <a-col :span="7">
                <p></p>
              </a-col>
              <a-col :span="5">
                <p></p>
              </a-col>
              <a-col :span="5">
                <p></p>
              </a-col>
            </a-row>
            <div v-if="scheduleList.length>0">
              <a-row :gutter="20" class="row" v-for="(el,i) in scheduleList" :key="i">
                <a-col :span="5">
                  <a-form-item>
                    <a-input
                      placeholder=""
                      :disabled="!editStatus"
                      v-decorator="[`scheduleList[${i}].groupDesc`,{initialValue:el.groupDesc,rules: [{required:true,message:``}]}]"
                    />
                  </a-form-item>
                </a-col>
                <a-col :span="7">
                  <a-form-item>
                    <a-date-picker
                      showTime
                      format="YYYY-MM-DD HH:mm"
                      placeholder=""
                      style="width:100%"
                      :disabled="!editStatus || i>0"
                      v-decorator="[`scheduleList[${i}].startTimeStr`,{initialValue: el.startTimeStr?moment(el.startTimeStr, 'YYYY-MM-DD HH:mm'):el.startTimeStr,rules: [{required:true,message:``}]}]"
                      @change="changeStartTime"
                    />
                  </a-form-item>
                </a-col>
                <a-col :span="5">
                  <a-form-item>
                    <a-input-number
                      :min="1"
                      style="width:100%"
                      placeholder=""
                      :disabled="!editStatus"
                      @change="changeContinueHours"
                      v-decorator="[`scheduleList[${i}].continueHours`,{initialValue:el.continueHours,rules: [{required:true,message:``}]}]"
                    />
                  </a-form-item>
                </a-col>
                <a-col :span="5">
                  <a-form-item>
                    <a-input-number
                      :min="0"
                      style="width:100%"
                      placeholder=""
                      :disabled="!editStatus"
                      v-decorator="[`scheduleList[${i}].restHours`,{initialValue:el.restHours,rules: [{required:true,message:``}]}]"
                    />
                  </a-form-item>
                </a-col>
                <a-col :span="1">
                  <a-icon
                    type="minus-circle"
                    v-if="editStatus && i>0"
                    @click="removeLoopClass(i,el)"
                  />
                </a-col>
              </a-row>
            </div>
            <div class="no_data" v-else></div>
          </div>
          <div class="btn_add" v-if="editStatus">
            <a-button type="primary" icon="plus" @click="openModal"></a-button>
          </div>
        </a-form>
      </div>
    </div>
    <a-modal
      title=""
      v-model="modalVisible"
      width="450px"
      okText=""
      @ok="addClass()"
    >
      <a-table
        size="small"
        class="table_blue"
        :columns="columns"
        :dataSource="classList"
        :pagination="false"
        rowKey="groupId"
      >
        <div slot="operation" slot-scope="text,record,index">
          <a-button type="primary" @click="renewClass(index,record)" size="small"></a-button>
          <a-popconfirm title="?" @confirm="() => removeClass(record)">
            <a-button size="small"></a-button>
          </a-popconfirm>
        </div>
      </a-table>
    </a-modal>
  </div>
</template>
<script>
import request from "../utils/request";
import util from "../utils/util";
import moment from "moment";
const columns = [
  {
    title: "ID",
    dataIndex: "groupId",
    scopedSlots: { customRender: "groupId" }
  },
  {
    title: "",
    dataIndex: "groupDesc",
    scopedSlots: { customRender: "groupDesc" }
  },
  {
    title: "",
    dataIndex: "operation",
    scopedSlots: { customRender: "operation" }
  }
];

export default {
  name: "mt-class",
  data() {
    return {
      columns,
      editStatus: false,
      scheduleList: [],
      classList: [],
      modalVisible: false,
      dataClass: {
        groupDesc: "",
        startTimeStr: null,
        continueHours: "",
        restHours: ""
      }
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  created() {
    this.initClassSchedule();
    this.initClassList();
  },
  methods: {
    moment,
    // 
    initClassSchedule() {
      let url = `/Service/API/V1/CHP/Group/getGroupInfoList`;
      request.get(url).then(res => {
        this.scheduleList = res.data;
      });
    },
    // 
    initClassList() {
      let url = `/Service/API/V1/CHP/Group/getClassGroupList`;
      request.get(url).then(res => {
        this.classList = res.data;
      });
    },
    removeLoopClass(index, el) {
      if (el.groupLoopId) {
        let url = `/Service/API/V1/CHP/Group/delete/${el.groupLoopId}`;
        request.delete(url).then(res => {
          this.$message.success("");
          this.initClassSchedule();
          this.initClassList();
        });
      } else {
        this.scheduleList.splice(index, 1);
      }
      this.updateScheduleList();
    },
    removeClass(data) {
      let url = `/Service/API/V1/CHP/Group/deleteClassGroup`;
      request.post(url, data).then(res => {
        this.initClassList();
      });
    },
    insertClass(data) {
      if (data) {
        let insertData = Object.assign({}, this.dataClass, data);
        this.scheduleList.push(insertData);
      } else {
        this.scheduleList.push(this.dataClass);
      }
      this.updateScheduleList();
    },
    openModal() {
      if (this.classList.length > 0) {
        this.modalVisible = true;
      } else {
        this.insertClass();
      }
    },
    addClass(data) {
      this.insertClass(data);
      this.modalVisible = false;
    },
    cancelSave() {
      this.form.resetFields();
      this.editStatus = false;
    },
    renewClass(i,data){
      this.classList.splice(i,1)
      this.addClass(data)
    },
    save() {
      let self = this;
      let url = "/Service/API/V1/CHP/Group/updateGroupInfo";
      this.form.validateFields((err, value) => {
        console.log(value, err);
        if (!err) {
          let data = JSON.parse(JSON.stringify(self.scheduleList));
          data = data.map((item,i)=>{
            let newItem = value.scheduleList[i]
            item = Object.assign({},item,newItem,{startTimeStr:util.formatTime(newItem.startTimeStr._d).timeToH})
           
            return item;
          })
          console.log(data)
          request.post(url,data).then(res=>{
            self.$message.success('')
            this.initClassSchedule()
            this.initClassList()
            self.editStatus = false;
          })
        }
      });
    },
    computeDate(timeStr, hours) {
      let date = new Date(timeStr);
      date = date.setHours(date.getHours() + hours);
      return util.formatTime(date).timeToH;
    },
    setClassListTime(list) {
      let self = this;
      if (list.length <= 1) {
        return;
      }
      for (let i = 1; i < list.length; i++) {
        let preClass = list[i - 1];
        if (preClass.continueHours && preClass.startTimeStr) {
          list[i].startTimeStr = self.computeDate(
            preClass.startTimeStr,
            preClass.continueHours
          );
        } else {
          list[i].startTimeStr = null;
        }
        
      }
      this.scheduleList = list
      
    },
    changeStartTime(e) {
      this.updateScheduleList();
    },
    changeContinueHours(e, a) {
      this.updateScheduleList();
    },
    updateScheduleList() {
      this.$nextTick(() => {
        let formData = this.form.getFieldsValue();
        let data = [];
        if (formData.scheduleList.length > 1) {
          data = formData.scheduleList.map((item, i) => {
            console.log(typeof(item.startTimeStr))
            let startTimeStr = item.startTimeStr && typeof('object')
              ? util.formatTime(item.startTimeStr._d).timeToH
              : null;
            item = Object.assign({}, this.scheduleList[i], item, { startTimeStr });
            return item;
          });
          this.setClassListTime(data);
        }
      });
    }
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  }
};
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>


