<template>
  <div id="MtAlarm_component" class="common-container">
    <a-form class="filter_box" :form="formData" layout="inline">
      <a-form-item label="">
        <a-select v-decorator="['gradeId', {initialValue: 0}]" placeholder="">
          <a-select-option
            v-for="(optionItem, optionIndex) in alarmLevelData"
            :key="optionIndex"
            :value="optionItem.value"
          >{{optionItem.label}}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="">
        <a-select v-decorator="['stateId', {initialValue: 0}]" placeholder="">
          <a-select-option
            v-for="(optionItem, optionIndex) in alarmStatusData"
            :key="optionIndex"
            :value="optionItem.value"
          >{{ optionItem.label }}</a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="">
        <a-range-picker
        v-decorator="['alarmTime']"
          :showTime="{
        hideDisabledOptions: true,
        defaultValue: [moment('00:00:00', 'HH:mm:ss'), moment('23:59:59', 'HH:mm:ss')]
      }"
          format="YYYY-MM-DD HH:mm:ss"
          @change="changeTime"
        />
      </a-form-item>
      <a-form-item label="">
        <a-input v-decorator="['keyWord']" placeholder="" />
      </a-form-item>
      <a-form-item>
        <a-button type="primary" @click="toSearch"></a-button>
        <a-button style="margin-left:20px;" @click="toReset"></a-button>
      </a-form-item>
    </a-form>
    <a-table id="table_blue" :columns="columns" size="small" :dataSource="data" :pagination="pageOptions" bordered rowKey="alarmDesc">
      <template slot="alarmGradeId" slot-scope="text, record">
        <span>{{text == 1 ? '' : text == 2 ? '' : text == 3 ? '' : '-'}}</span>
      </template>
      <template slot="alarmStatus" slot-scope="text, record">
        <span>{{text == 1 ? '' : text == 2 ? '' : text == 3 ? '' : '-'}}</span>
      </template>
      <template slot="operate" slot-scope="text, record">
        <a-button v-if="record.IsManualReset" type="primary" @click="resetAlarm(record)"></a-button>
      </template>
    </a-table>
  </div>
</template>
<script>
import moment from 'moment'
import request from '../utils/request'
const columns = [
  {
    title: '',
    width: 90,
    dataIndex: 'alarmGradeId',
    scopedSlots: { customRender: 'alarmGradeId' }
  },
  {
    title: '',
    dataIndex: 'partDesc',
    scopedSlots: { customRender: 'partDesc' }
  },
  {
    title: '',
    dataIndex: 'alarmDesc',
    scopedSlots: { customRender: 'alarmDesc' }
  },
  {
    title: '',
    dataIndex: 'alarmStatus',
    scopedSlots: { customRender: 'alarmStatus' }
  },
  {
    title: '',
    dataIndex: 'alarmBeginTime',
    scopedSlots: { customRender: 'alarmBeginTime' }
  },
  {
    title: '',
    dataIndex: 'alarmDetails',
    scopedSlots: { customRender: 'alarmDetails' }
  },
  {
    title: '',
    dataIndex: 'alarmEndTime',
    scopedSlots: { customRender: 'alarmEndTime' }
  },
  {
    title: '',
    dataIndex: 'operate',
    scopedSlots: { customRender: 'operate' }
  }

]
const requestUrls = {
  getAlarmLevel: '/Service/API/V1/CPH/alarm/dictionary',
  getAlarmList: '/Service/API/V1/CPH/alarm/getAlarmInfo',
  setAlarm: '/Service/API/V1/CPH/alarm/resetAlarm'
}
export default {
  name: 'mt-alarm',
  data () {
    return {
      alarmLevelData: [
        { label: '', value: 0 },
        { label: '', value: 1 },
        { label: '', value: 2 },
        { label: '', value: 3 }
      ],
      alarmStatusData: [
        { label: '', value: 0 },
        { label: '', value: 1 },
        { label: '', value: 2 },
        { label: '', value: 3 }
      ],
      columns,
      data: [],
      formData: this.$form.createForm(this),
      postData: {
        alarmTime: [],
        gradeId: 0,
        stateId: 0,
        keyWord: '',
        pageNum: 10,
        currentPage: 1
      },
      pageOptions: {
        defaultPageSize: 10,
        showQuickJumper: true,
        showSizeChanger: true,
        pageSizeOptions: ['10', '20', '50'],
        total: 0,
        onShowSizeChange: (current, size) => {
          this.postData.pageNum = size
          this.postData.currentPage = 1
          this.requestFormList()
        },
        onChange: (page, pageSize) => {
          // 
          this.postData.currentPage = page
          this.requestFormList()
        }
      },
      requestUrls
    }
  },
  mounted () {
    this.getAlarmLevel()
    this.requestFormList()
  },
  methods: {
    moment,
    requestFormList () {
      let that = this
      let url = this.requestUrls.getAlarmList
      request({
        url: url,
        method: 'post',
        data: that.postData
      }).then(res => {
        // alert('success')
        that.data = res.data.alarmInfoList
        that.pageOptions.total = res.data.totalCount
        that.postData.currentPage = res.data.page
      }).catch(() => {
        // alert('error')
      })
    },
    getAlarmLevel () {
      let url = this.requestUrls.getAlarmLevel
      request({
        url: url,
        method: 'get'
      }).then(res => {
        // alert('success')
      }).catch(() => {
        // alert('error')
      })
    },
    toSearch () {
      this.formData.validateFields((err, values) => {
        if (!err) {
          this.postData = Object.assign(this.postData, values)
          this.requestFormList()
        }
      })
    },
    toReset () {
      this.formData.resetFields()
      this.requestFormList()
    },
    changeTime () {},
    resetAlarm (record) {
      let that = this
      this.$confirm({
        title: '',
        content: h => <div><p><span></span><span style="font-size:14px;font-weight:bold;">{record.alarmDesc}</span></p><p><span></span><span style="font-size:14px;font-weight:bold;">{record.decisionRules}</span></p><p>?</p></div>,
        onOk () {
          let url = that.requestUrls.setAlarm
          request({
            url: url,
            method: 'post',
            data: { alarmId: record.alarmId }
          }).then(res => {
            that.requestFormList()
          }).catch(() => {
            // alert('error')
          })
        },
        onCancel () {
          console.log('Cancel')
        },
        class: 'test'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../style/ant-style-cover.scss';
form .ant-select {
  min-width: 100px !important;
}
.filter_box{
  text-align: right;
}
#table_blue{
  margin-top: 20px;
}
</style>



